@-webkit-keyframes fade {
  0% {
    opacity: 0; }
  50%, 60% {
    opacity: 1; } }

@keyframes fade {
  0% {
    opacity: 0; }
  50%, 60% {
    opacity: 1; } }
    main #audioContainer #shareLink div.ani {
